import React from 'react';
import './App.css';

import Header from './Components/Header';
import ListUsers from './Components/ListUsers';
import ViewProfile from './Components/ViewProfile';

class App extends React.Component {

  constructor() {
    super();
    this.state = {
      clickedUser: {},
      searchField: '',
      users: [
        {
          id: 1,
          email: "jaydon_gislason59@yahoo.com",
          phone: "(998) 970-2799",
          address: {
            city: "South Nyah",
            state: "New York",
            country: "Equatorial Guinea",
            zipCode: "16308-9260",
            countryCode: "AX",
            streetAddress: "3585 Lucinda Valleys"
          },
          lastName: "Von",
          firstName: "Walter",
          profileImage: "https://s3.amazonaws.com/uifaces/faces/twitter/jeremyworboys/128.jpg"
        }
      ]
    }
    this.clickHandler = this.clickHandler.bind(this);
  }

  clickHandler(user) {
    this.setState({
      clickedUser: user
    })
  }

  searchFilter() {
    return this.state.users.filter(value => ((value.firstName + ' ' + value.lastName).toLowerCase()).includes((this.state.searchField).toLowerCase()));
  }

  setSearchFild(text) {
    this.setState({
      searchField: text
    })
  }

  resetClickedUser = () => {
    setTimeout(() => {
      this.setState({
        clickedUser: {}
      })
    }, 500)
  }

  componentDidMount() {
    fetch('https://mock-io.herokuapp.com/users')
      .then(response => response.json())
      .then(data => {
        this.setState({
          users: data
        })
      })
  }

  render() {
    return (
      <div>
        <Header />
        <input style={searchInputStyle} type='text' value={this.state.searchField} onChange={(e) => this.setSearchFild(e.target.value)} placeholder='Search' />
        <ListUsers users={this.searchFilter()} clickHandler={this.clickHandler} />
        {this.state.clickedUser.hasOwnProperty('firstName') ? <ViewProfile reset={this.resetClickedUser} user={this.state.clickedUser} /> : <React.Fragment />}
      </div>
    );
  }
}

const searchInputStyle = {
  width: 'calc(100% / 1.2)',
  lineHeight: '40px',
  padding: '10px',
  border: 'transparent',
  backgroundColor: 'transparent',
  display: 'block',
  textAlign: 'center',
  fontSize: '20px',
  margin: '10px auto',
  borderRadius: '10px',
  boxShadow: '0px 0px 2px grey'
}

export default App;
