import React from 'react'
import backImage from './Images/left-arrow.png';

export default class ViewProfile extends React.Component {

  constructor() {
    super();
    this.state = {
      top: window.screen.height,
    }
  }

  disableScroll() {
    let x = window.pageXOffset;
    let y = window.pageYOffset;

    window.onscroll=()=> {
      window.scrollTo(x, y);
    }
  }

  enableScroll() {
    window.onscroll = () => {};
  }

  getPopUpTop() {
    return this.state.top;
  }

  componentDidMount() {
    this.disableScroll();
    document.addEventListener('keydown', this.initDownAnimation);
      this.setState({
        top: 0
      })
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.initDownAnimation);
  }

  initDownAnimation = () => {
    this.props.reset();
    this.enableScroll();
    this.setState({
      top: window.screen.height
    })
  }

  render() {
    const { user } = this.props;
    return (
      <div tabIndex='1' style={{ ...popUpStyle, top: this.getPopUpTop() + 'px' }} className='pop-up'>
        <img style={backImageStyle} onClick={() => this.initDownAnimation()} src={backImage} alt='' />
        <ul style={listStyle}>
          <li><span style={spanStyle}>{(user.firstName || '') + ' ' + (user.lastName || '')}</span></li>
          <li><img style={imageStyle} src={user.profileImage || ''} alt=''></img></li>
          <li><span style={spanStyle} className='info-heading'>Address</span></li>
          <li>{user.address ? user.address.city : ''}</li>
          <li>{user.address ? user.address.state : ''}</li>
          <li>{user.address ? user.address.country : ''}</li>
          <li>{user.address ? user.address.zipCode : ''}</li>
          <li>{user.address ? user.address.countryCode : ''}</li>
          <li>{user.address ? user.address.streetAddress : ''}</li>
          <li><span style={spanStyle}>Contact</span></li>
          <li>{user.email}</li>
          <li>{user.phone}</li>
        </ul>
      </div>
    )
  }
}

const popUpStyle = {
  margin: '5px',
  backgroundColor: '#000000cc',
  borderRadius: '10px',
  boxShadow: '0px 0px 2px grey',
  width: 'calc(100% - 20px)',
  height: 'calc(100% - 20px)',
  position: 'fixed',
  transition: 'top 0.5s',
  left: '0'
}

const listStyle = {
  display: 'flex',
  flexFlow: 'column wrap',
  alignItems: 'center',
  margin: '10px',
  padding: '5px',
  lineHeight: '25px',
  color: '#fff'
}

const imageStyle = {
  borderRadius: '10px',
}

const backImageStyle = {
  padding: '10px',
  cursor: 'pointer'
}

const spanStyle = {
  color: '#3f3',
  lineHeight: '50px'
}

